___CSS_LOADER_EXPORT___.push([module.id, `.googleIconPicker__content {
  margin: auto auto;
  padding: 0 0 40px;
  width: 90%; 
  height: 80%; 
  position: relative;
  overflow: hidden;
  max-width: none;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.googleIconPicker {
  background: #fcfcfc;
  padding: 20px;
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: transparent;
  display: flex;
}

.googleIconPicker__iconWrapper {
  font-variation-settings: 'FILL' var(--fill), 'wght' var(--weight), 'GRAD' var(--grade), 'opsz' var(--opticalsize);
  background: transparent;
  border: 0;
  border-radius: 8px;
  cursor: pointer;
  height: 128px;
  position: relative;
  user-select: none;
  width: 96px;
}

.googleIconPicker__iconWrapper:hover {
  background-color: rgba(13, 25, 44, 10%);
}

.googleIconPicker__iconWrapper > span {
  pointer-events: none;
} 

.googleIconPicker__icon.googleIconPicker__icon {
  display: block;
  margin-bottom: 20px;
  font-size: 48px;
}

.googleIconPicker__icons {
  display: flex;
  flex-wrap: wrap;
  gap: 11px;
  overflow-y: scroll;
  overflow-x: hidden;
  height: 100%;
  align-content: flex-start;
  padding: 10px 8px;
}

.googleIconPicker__close {
  position: absolute;
  top: 8px;
  right: 20px;
  z-index: 10;
}

.googleIconPicker__search {
  display: flex;
  align-items: center;
  gap: 6px;
}

.googleIconPicker__iconName {
  font-size: 12px;
  font-family: Helvetica,sans-serif;
}

.googleIconPicker__overlay {
  background: rgba(0,0,0, 0.2);
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.googleIconPicker__filterBar {
  display: flex;
  align-items: center;
  gap: 12px;
  padding-bottom: 6px;
}

.googleIconPicker__customization{
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.googleIconPicker__customization > div{
  display: flex;
  /* flex-wrap: wrap; */
  gap: 6px;
  align-items: center;
}

.googleIconPicker__customization label {
  white-space: nowrap;
}

.googleIconPicker__customization .gjs-select {
  min-width: 50px;
}`, "",{"version":3,"sources":["webpack://./src/iconPicker.css"],"names":[],"mappings":"AAAA;EACE,iBAAiB;EACjB,iBAAiB;EACjB,UAAU;EACV,WAAW;EACX,kBAAkB;EAClB,gBAAgB;EAChB,eAAe;AACjB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,eAAe;EACf,iBAAiB;AACnB;;AAEA;;EAEE,YAAY;EACZ,qBAAqB;EACrB,eAAe;AACjB;;AAEA;EACE,mBAAmB;EACnB,aAAa;EACb,YAAY;EACZ,eAAe;EACf,MAAM;EACN,OAAO;EACP,YAAY;EACZ,WAAW;EACX,uBAAuB;EACvB,aAAa;AACf;;AAEA;EACE,iHAAiH;EACjH,uBAAuB;EACvB,SAAS;EACT,kBAAkB;EAClB,eAAe;EACf,aAAa;EACb,kBAAkB;EAClB,iBAAiB;EACjB,WAAW;AACb;;AAEA;EACE,uCAAuC;AACzC;;AAEA;EACE,oBAAoB;AACtB;;AAEA;EACE,cAAc;EACd,mBAAmB;EACnB,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,eAAe;EACf,SAAS;EACT,kBAAkB;EAClB,kBAAkB;EAClB,YAAY;EACZ,yBAAyB;EACzB,iBAAiB;AACnB;;AAEA;EACE,kBAAkB;EAClB,QAAQ;EACR,WAAW;EACX,WAAW;AACb;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,QAAQ;AACV;;AAEA;EACE,eAAe;EACf,iCAAiC;AACnC;;AAEA;EACE,4BAA4B;EAC5B,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,YAAY;EACZ,WAAW;AACb;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;EACT,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,eAAe;EACf,SAAS;AACX;;AAEA;EACE,aAAa;EACb,qBAAqB;EACrB,QAAQ;EACR,mBAAmB;AACrB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,eAAe;AACjB","sourcesContent":[".googleIconPicker__content {\n  margin: auto auto;\n  padding: 0 0 40px;\n  width: 90%; \n  height: 80%; \n  position: relative;\n  overflow: hidden;\n  max-width: none;\n}\n\n.close {\n  color: #aaa;\n  float: right;\n  font-size: 28px;\n  font-weight: bold;\n}\n\n.close:hover,\n.close:focus {\n  color: black;\n  text-decoration: none;\n  cursor: pointer;\n}\n\n.googleIconPicker {\n  background: #fcfcfc;\n  padding: 20px;\n  z-index: 100;\n  position: fixed;\n  top: 0;\n  left: 0;\n  height: 100%;\n  width: 100%;\n  background: transparent;\n  display: flex;\n}\n\n.googleIconPicker__iconWrapper {\n  font-variation-settings: 'FILL' var(--fill), 'wght' var(--weight), 'GRAD' var(--grade), 'opsz' var(--opticalsize);\n  background: transparent;\n  border: 0;\n  border-radius: 8px;\n  cursor: pointer;\n  height: 128px;\n  position: relative;\n  user-sel